@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

*{
  font-family: "Poppins";
}

main {
  min-height: 80vh;
}

h3 {
  padding: 1rem 0;
}

.rating span {
  margin: 0.1rem;
}

#prod_name {
  text-decoration: none;
}
#prod_name:hover {
  text-decoration: underline;
}

#btz {
  margin: auto;
  display: flex;
}

.carousel-item-next,
.carousel-item-prev,
.carousel-item.active {
  display: flex;
}
.carousel-caption {
  position: absolute;
  top: 0;
}

.carousel-caption h2 {
  color: #fff;
}

.carousel img {
  height: 300px;
  padding: 30px;
  margin: 40px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
}
.carousel a {
  margin: 0 auto;
}
.page-link{
  background-color: #f2efef;
  border-radius: 10px;
}
.page-item{
  margin-left: 10px;
  border-radius: 10px;
}
/* .page-link:hover{
  border: none;
} */
@media (max-width: 900px) {
  .carousel-caption h2 {
    font-size: 2.5vw;
  }
}