.leaderboard {
    width: 60%;
    /* width: 100%; */
    border-radius: 12px;
}
.leaderboard header {
    --start: 15%;
    height: 100px;
    /* background-image: repeating-radial-gradient(circle at var(--start), transparent 0%, transparent 10%, rgba(54, 89, 219, .33) 10%, rgba(54, 89, 219, .33) 17%), linear-gradient(to right, #5b7cfa, #3659db); */
    background: linear-gradient(130deg, #0b13a7 0%, #0040FF 100%);
    background: linear-gradient(87deg, #172b4d 0, #1a174d 100%) !important;
    color: #fff;
    position: relative;
    border-radius: 12px 12px 0 0;
    overflow: hidden;
}
.leaderboard header .leaderboard__title {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: calc(var(--start) * .75);
    transform: translateY(-50%);
    text-transform: uppercase;
    margin: 0;
    color: white;
}
.leaderboard header .leaderboard__title span {
    display: block;
}
.leaderboard header .leaderboard__title--top {
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 6.5px;
}
.leaderboard header .leaderboard__title--bottom {
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 3.55px;
    opacity: 0.65;
    transform: translateY(-2px);
}
.leaderboard header .leaderboard__icon {
    fill: #fff;
    opacity: 0.35;
    width: 50px;
    position: absolute;
    top: 50%;
    left: var(--start);
    transform: translate(-50%, -50%);
}
.leaderboard__profiles {
    /* background-color: #fff; */
    border-radius: 0 0 12px 12px;
    padding: 15px 10px;
    display: grid;
    row-gap: 8px;
    justify-content: inherit;
}
.leaderboard__profile {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    align-items: center;
    padding: 10px 30px 10px 10px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
    /* cursor: pointer; */
    transition: transform 0.35s cubic-bezier(0.7, 0.98, 0.86, 0.98), box-shadow 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98);
    background-color: #fff;
    /* max-height: 80px !important; */
    margin-bottom: 10px;
    margin-top: 10px;
}
.leaderboard__profile__new{
  /* display: grid; */
    grid-template-columns: 1fr 3fr 1fr;
    align-items: center;
    padding: 10px 30px 10px 10px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 5px 7px -1px rgba(51, 51, 51, 0.23);
    transition: transform 0.35s cubic-bezier(0.7, 0.98, 0.86, 0.98), box-shadow 0.25s cubic-bezier(0.7, 0.98, 0.86, 0.98);
    background-color: #fff;
    margin-bottom: 10px;
    margin-top: 10px;
}
.leaderboard__profile__new:hover {
    /* transform: scale(1.05); */
    box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
}
.leaderboard__profile:hover {
    /* transform: scale(1.05); */
    box-shadow: 0 9px 47px 11px rgba(51, 51, 51, 0.18);
}
.leaderboard__picture {
    max-width: 100%;
    width: 65px;
    border-radius: 50%;
    box-shadow: 0 0 0 5px #ebeef3, 0 0 0 10px #f3f4f6;
}
.leaderboard__name {
    color: #979cb0;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.64px;
    margin-left: 12px;
}
.leaderboard__value {
    color: #35d8ac;
    font-weight: 700;
    font-size: 34px;
    text-align: right;
}
.leaderboard__value > span {
    opacity: 0.8;
    font-weight: 600;
    font-size: 13px;
    margin-left: 3px;
}
