.main_container{
  max-width: 600px;
  margin: auto;
}
.review_container{
    width: 100%;
    /* min-width: 700px; */
    background-color: #fff;
    padding: 25px 30px;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0,0,0,0.15);
    margin-top: 30px;
  }
  .review_container .title{
    font-size: 25px;
    font-weight: 500;
    position: relative;
  }
  .review_container .title::before{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    height: 3px;
    width: 30px;
    border-radius: 5px;
    background: linear-gradient(135deg, #71b7e6, #9b59b6);
  }
  .content  .user-details{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: space-between;
    margin: 20px 0 12px 0;
  }
   .user-details .input-box{
    margin-bottom: 15px;
    /* width: calc(100% / 2 - 20px); */
    /* border: 1px linear-gradient(130deg, #0b13a7 0%, #0040FF 100%) solid; */
  }
  .review_header{
    font-size: 20px;
    padding-bottom: 0;
    font-weight: 500;
   }
  
   .input-box span.details{
    display: block;
    font-weight: 500;
    margin-bottom: 5px;
    font-size: 15px;
  }
  .user-details .input-box input{
    height: 45px;
    width: 100%;
    outline: none;
    font-size: 16px;
    border-radius: 5px;
    padding-left: 15px;
    border: 1px solid #ccc;
    border-bottom-width: 2px;
    transition: all 0.3s ease;
  }
  .gender {
    height: 45px;
    width: 100%;
    outline: none;
    font-size: 16px;
    border-radius: 5px;
    padding-left: 15px;
    border: 1px solid #ccc;
    border-bottom-width: 2px;
    transition: all 0.3s ease;
  }
  .gender option {
    height: 45px;
    width: 70%;
    outline: none;
    font-size: 16px;
    border-radius: 5px;
    padding-left: 15px;
    border: 1px solid #ccc;
    border-bottom-width: 2px;
    transition: all 0.3s ease;
  }
    .button{
     height: 45px;
     margin: 35px 0
   }
    .button input{
     height: 100%;
     width: 100%;
     border-radius: 5px;
     border: none;
     color: #fff;
     font-size: 18px;
     font-weight: 500;
     letter-spacing: 1px;
     cursor: pointer;
     transition: all 0.3s ease;
     background: linear-gradient(135deg, #71b7e6, #9b59b6);
   }
    .button input:hover{
    /* trans: scale(0.99); */
    background: linear-gradient(-135deg, #71b7e6, #9b59b6);
    }
    .custom-file-upload {
      border: 1px solid #ccc;
      display: inline-block;
      padding: 6px 12px;
      cursor: pointer;
  }
  .cart-btn {
    font-size: 1rem;
    line-height: 1.5;
    display: inline-block;
    padding: 12px 30px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    border: 1px solid transparent;
    border-radius: .375rem;
    color: #fff;
    border-color: #5e72e4;
    background-color: #5e72e4;
    box-shadow: 0 4px 6px rgba(50, 50, 93, .11), 0 1px 3px rgba(0, 0, 0, .08);
  }
  .cart-btn:hover {
    background-color: white;
    color: #5e72e4;
  }
  /* input[type="file"] {
      display: none;
  } */
  @media(max-width : 1200px) {
    body {
      height : 100vh;
    }
    
    .content  .user-details{
      max-height: 700px;
    }
  }
   @media(max-width: 700px){
    body {
      height : 100vh;
    }
    .main_container{
      margin-left: 20px;
      margin-right: 20px;
    }
   .container{
    max-width: 100%;
  }
   .user-details .input-box{
      margin-bottom: 15px;
      width: 100%;
    }
     .category{
      width: 100%;
    }
    .content  .user-details{
      max-height: 300px;
      
    }
    .user-details::-webkit-scrollbar{
      width: 5px;
    }
    }
    @media(max-width: 459px){
    .container .content .category{
      flex-direction: column;
    }
  }